<template>
<div>
 <h1>{{msg}}</h1>
<img src="http://r-ec.bstatic.com/images/hotel/max1024x768/261/26173790.jpg" class="img-fluid" alt="" id="image">  
    <br><br>
    <label><b>Nom d'utilisateur</b></label>
    <input type="text"  placeholder="nom d'utilisateur" v-model="user" name="user" required >

    <label><b>Mot de passe</b></label>
    <input type="password" placeholder="mot de passe" v-model="psw" name="psw" required >

    <button  class="btn btn-primary"  v-on:click="login(user,psw)">Login</button>
</div>
</template>


<script>

import router from '../router/index.js';

export default {
  name: 'authentification',
  data () {
     
    return {
      msg: 'Hotel Phebus : Adminitration',
      user :"",
     psw : ""
    }
  },
  methods : {
    login : function (name,pwd){
         if (name=="redadmin" && pwd=="redcarpet2017"){
                    router.push({name:'indexreservation'})

         }
    }
  }
}
</script>

<style>
#image {
  height :500px;
}
</style>


