<template>
    <div>
    
        <br>
        <div class="form-group row">
            <label for="example-text-input" class="col-2 col-form-label">Prnom</label>
            <div class="col-9">
                <input class="form-control" type="text" v-model="reservat.firstName">
            </div>
        </div>
    
        <div class="form-group row">
            <label for="example-text-input" class="col-2 col-form-label">Nom</label>
            <div class="col-9">
                <input class="form-control" type="text" v-model="reservat.lastName">
            </div>
        </div>
    
        <div class="form-group row">
            <label for="example-email-input" class="col-2 col-form-label">Email</label>
            <div class="col-9">
                <input class="form-control" type="email" v-model="reservat.email">
            </div>
        </div>
    
        <div class="form-group row">
            <label for="example-tel-input" class="col-2 col-form-label">Telephone</label>
            <div class="col-9">
                <input class="form-control" type="tel" v-model="reservat.phone">
            </div>
        </div>
 
        <div class="form-group row">
            <label for="example-text-input" class="col-2 col-form-label">Nombre de jours</label>
            <div class="col-9">
                <input class="form-control" type="text" v-model="reservat.days">
            </div>
        </div>

       <div class="form-group row">
            <label for="example-text-input" class="col-2 col-form-label">Date</label>
            <div >
                <datepicker class="col-3 offset-2 centered"  language="fr" :inline="true" v-model="reservat.date"></datepicker>
            </div>
        </div>
        <div >
            <button class="btn btn-success" v-on:click="storage(reservat)">Sauvegarder</button>
    
        </div>
    
    
    
    </div>
</template>

<script>
    import Datepicker from 'vuejs-datepicker';
    import router from '../router/index.js';
    export default {
        data() {
            return {
                reservat: {
                    firstName: "",
                    lastName: "",
                    email: "",
                    phone: "",
                    days: ""
                }
            }
        },
        methods: {
            getres: function(id) {
    
                return new Promise((resolve, reject) => {
                    for (var i = 0; i < this.reservations.length; i++) {
                        if (id === this.reservations[i].id) {
                            resolve(this.reservations[i]);
                        }
                    }
                    reject();
    
                })
            },
            storage: function(reservation) {
                for (var i = 0; i < this.reservations.length; i++) {
                    if (this.reservations[i].id === reservation.id) {
                        this.reservations[i] = reservation;
                    }
                }
                localStorage.reservations = JSON.stringify(this.reservations);
                router.push({
                    name: 'indexreservation'
                })
            }
        },
        created: function() {
            console.log('ok');
            this.reservations = JSON.parse(localStorage.reservations);
            this.getres(this.$route.params.id).then((res) => {
                console.log(res);
                this.reservat = res
            }).catch(() => {
                console.log('error');
            })
    
        },
        components: {
            'datepicker': Datepicker
    
        }
    
    }
</script>


