<template>
<div>
    <br>
    <h2 class="align-self-center">Liste de rservations</h2>

    <table style="width:100%">
        <br>
        <tr>
            <td>
                <h4>Prnom</h4>
            </td>
            <td>
                <h4>Nom</h4>
            </td>
            <td>
                <h4>email</h4>
            </td>
            <td>
                <h4>Tlphone</h4>
            </td>
    
            <td>
                <h4>Date de rservation</h4>
            </td>
            <td>
                <h4>Dure</h4>
            </td>
    
    
            <td>
                <h4>actions</h4>
            </td>
        </tr>
        <tr v-for="d in reservation">
            <td>{{d.firstName}}</td>
            <td>{{d.lastName}}</td>
            <td>{{d.email}}</td>
            <td>{{d.phone}}</td>
            <td>{{d.date |formatdate}} </td>
            <td>{{d.days}}</td>
            <td>
                <router-link :to="{ name: 'editreservation', params: {id:d.id}}">
                    <button class="btn btn-info">Dtails</button>
                </router-link>
            </td>
            <td><button class="btn btn-danger" v-on:click="delet(d.id)">Supprimer</button></td>
        </tr>
    </table>
    </div>
</template>


<script>
    export default {
        name: 'indexreservation',
        data() {
            return {
    
                reservation: []
            }
        },
    
        created: function() {
            this.reservation = JSON.parse(localStorage.reservations);
    
        },
        methods: {
            getindex: function(id) {
                for (var i = 0; i < this.reservation.length; i++) {
                    if (this.reservation[i].id === id) {
                        return i;
                    }
                }
            },
            delet: function(id) {
                this.reservation.splice(this.getindex(id), 1);
                localStorage.reservations = JSON.stringify(this.reservation);
            }
        }
    }
</script>




