<template>
    <table class="table table-hover">
        <tr>
            <td></td>
            <td>petit dej</td>
            <td>petit dej + dej</td>
            <td>petit dej + dej + diner</td>
        </tr>
        <tr>
            <td>1 personne</td>
            <td><input v-model="prices.p[0]"></input>
            </td>
            <td><input v-model="prices.pd[0]"></input>
            </td>
            <td><input v-model="prices.pdd[0]"></input>
            </td>
    
    
    
        </tr>
        <tr>
            <td>2 personnes</td>
            <td><input v-model="prices.p[1]"></input>
            </td>
            <td><input v-model="prices.pd[1]"></input>
            </td>
            <td><input v-model="prices.pdd[1]"></input>
            </td>
    
        </tr>
        <tr>
            <td>3 personnes</td>
            <td><input v-model="prices.p[2]"></input>
            </td>
            <td><input v-model="prices.pd[2]"></input>
            </td>
            <td><input v-model="prices.pdd[2]"></input>
            </td>
    
        </tr>
        <tr>
            <td>4 personnes</td>
            <td><input v-model="prices.p[3]"></input>
            </td>
            <td><input v-model="prices.pd[3]"></input>
            </td>
            <td><input v-model="prices.pdd[3]"></input>
            </td>

        </tr> 
        <br>
        <div  class="col offset-10">
                <button  class="btn btn-success col offset-10" v-on:click="storage()">submit</button>

        </div>
    </table>
</template>


<script>
    export default {
        name: 'tab',
        data() {
            return {
                prices: {
                    p: [100, 126, 181, 227],
                    pd: [120, 174, 241, 308],
                    pdd: [147, 223, 311, 402],
    
                }
            }
        },
        methods: {
            storage: function() {
                localStorage.prices = JSON.stringify(this.prices);
            },

            getprices : function (){
                this.prices = JSON.parse(localStorage.prices);
            }
        },
        created : function (){
            this.getprices()
        }
    
    }
</script>





