<template>
    <div>
        <nav class="navbar navbar-toggleable-md navbar-light bg-faded">
           
       
            <a class="navbar-brand hott" v-on:click="tabf()">Tarif</a>
            <a class="navbar-brand hott" v-on:click="resf()">Rservation</a>
            <div class="collapse navbar-collapse" id="navbarSupportedContent">
    
                <div class="col align-self-end ">                    
                        <button class="btn btn-outline-success my-2 my-sm-0" v-on:click="out()">Dconnexion</button>
                </div>
            </div>
        </nav>
    
        <transition name="fade" mode="out-in">
            <router-view></router-view>
        </transition>
    </div>
</template>


<script>
    import router from '../router/index.js';
    export default {
        name: 'admin',
    
        methods: {
            tabf: function() {
                router.push({
                    name: 'tab'
                })
            },
            resf: function() {
                router.push({
                    name: 'indexreservation'
                })
            },
            out: function() {
                router.push({
                    name: 'authentification'
                })
            }
    
        }
    }
</script>

<style scoped>
    .hott:hover{
       color: #22bf51 !important;
    }
</style>




